@font-face {
  font-family: "Bold";
  src: local("PTSansNarrow-Bold");
  src: url("../src/fonts/PTSansNarrow-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "Regular";
  src: local("PTSansNarrow-Regular");
  src: url("./fonts/PTSansNarrow-Regular.ttf") format("truetype");
}

* {
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Regular", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}

.logo {
  width: 300px;
  float: right;
  cursor: pointer;
}
