#custom-checkbox {
  width: 10px;
}

.recipePage {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: -1;
  display: flex;
  flex-direction: column;
  background-color: rgb(119, 60, 39);
}

.homeButton {
  position: absolute;
  background-color: transparent;
  width: 80px;
  height: 40px;
  margin-top: 1rem;
  margin-left: 2rem;
  font-size: 60px;
  color: orange;
}

.homeButton:hover {
  text-decoration: underline;
}

.recipePage--top {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.recipePage--left {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 49%;
  height: 100%;
  background-color: rgb(119, 60, 39);
}

.recipePage--title {
  margin: 6rem 0 2rem 0;
  text-align: center;
  font-size: 4rem;
}

.recipePage--image {
  border: solid 4px orange;
  border-radius: 12px;
  box-shadow: 5px 10px orange;
  min-width: 40%;
  height: 100%;
  object-fit: fill;
  max-width: 500px;
  margin: 1rem auto;
}

.recipePage--ingredients {
  width: 49%;
  font-size: 24px;
  display: flex;
  flex-direction: column;
  text-align: left;
  background-color: rgb(119, 60, 39);
}

.ingredientContainer {
  display: flex;
  flex-direction: row;
}

.recipePage--ingredient {
  margin-top: 1rem;
}

.instructions {
  background-color: rgb(119, 60, 39);
  border: solid 4px orange;
  color: white;
  margin-top: 1rem;
  font-size: 40px;
  width: 200px;
  height: 100px;
  border-radius: 12px;
}

.instructions:hover {
  background-color: orange;
  color: black;
}

.recipePage--bottom {
  margin-top: 3rem;
  text-align: center;
  height: 100%;
  background-color: rgb(119, 60, 39);
}

.recipePage--bottom--inputContainer {
  display: flex;
  flex-direction: row;
  text-align: center;
  font-size: 24px;
  margin: auto;
  justify-content: center;
}

.checkbox--input {
  margin: 1rem 0 0 0;
  height: 30px;
}

.item--container {
  margin-top: 1rem;
  width: 20%;
  text-align: left;
  height: auto;
  background-color: rgb(119, 60, 39);
}

.shoppingListButton {
  font-size: 24px;
  color: white;
  background-color: rgb(119, 60, 39);
  border: orange solid 3px;
  margin-top: 2rem;
  margin-bottom: 2rem;
  cursor: pointer;
  padding: 1rem;
  border-radius: 12px;
}

.shoppingListButton:hover {
  background-color: orange;
  color: black;
}

@media (min-width: 1920px) {
  .recipePage--container {
    width: 60%;
    margin: auto;
  }
}

@media (max-width: 1279px) {
  .recipePage--top {
    display: contents;
    margin: auto;
  }

  .recipePage--title {
    font-size: 3rem;
  }

  .recipePage--left {
    width: auto;
  }

  .recipePage--ingredients {
    margin: 0;
    padding: 1rem;
    font-size: 18px;
    width: 100%;
    height: 100%;
  }

  .recipePage--bottom {
    height: 100%;
    margin: 0;
    width: 100%;
    font-size: 18px;
    background-color: rgb(119, 60, 39);
  }

  .recipePage--bottom--inputContainer {
  }

  .item--container {
    width: 50%;
  }

  .checkbox--input {
    min-width: 100px;
  }
}
