.shoppingList {
  background-color: white;
  color: black;
}

.shoppingList--homeButton {
  position: absolute;
  background-color: transparent;
  width: 80px;
  height: 40px;
  top: 10px;
  left: 20px;
  font-size: 60px;
  color: orange;
}

.shoppingList--homeButton:hover {
  text-decoration: underline;
}
.shoppingList--title {
  color: black;
  text-align: center;
  margin-top: 6rem;
}

hr {
  background-color: white;
  margin: 2rem;
  height: 2px;
  border-top: orange solid 2px;
}

.shoppingList--ingredients {
  font-size: 24px;
  margin: auto;
  text-align: center;
  cursor: pointer;
}

.shoppingList--ingredient {
  color: black;
}

@media (min-width: 1920px) {
  .shoppingList--homeButton {
    left: 40rem;
  }
}
