.home {
  text-align: center;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: -1;

  background-color: rgb(119, 60, 39);
}

.home--title {
  font-family: "Bold";
  font-size: 100px;
  color: orange;
}

input {
  background-color: #fff;
  max-width: 700px;
  width: 30%;
  min-width: 300px;
  height: 50px;
  border-radius: 12px;
  color: black;
  font-size: 24px;
  text-align: center;
}

form {
  position: relative;
  z-index: 1;
  background-color: transparent;
  top: 10rem;
}

.search {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 400px;
  top: 2rem;
}

.search--background {
  overflow: hidden;
  width: 100%;
  height: 400px;
  position: absolute;
  z-index: 0;
  left: 0;
  right: 0;
  object-fit: cover;
  border-bottom: solid 4px orange;
  border-top: solid 4px orange;
}

.search-button {
  width: 100px;
  height: 50px;
  font-size: 20px;
  font-weight: 800;
  color: white;
  background-color: rgb(119, 60, 39);
  border-radius: 12px;
}

.recipes {
  position: relative;
  z-index: 1;
  margin-top: 5rem;
}

.recipeButton-link {
  text-decoration: none;
  color: white;
}

.recipeList--title {
  padding-top: 3rem;
  color: #fff;
}

.recipeList {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 2rem;
  font-size: 20px;
}

.loadButton {
  background-color: rgb(119, 60, 39);
  border: solid 4px orange;
  color: white;
  font-size: 40px;
  width: 200px;
  height: 100px;
  border-radius: 12px;
  margin-bottom: 2rem;
}

.loadButton:hover {
  background-color: orange;
  color: black;
}

@media (min-width: 1920px) {
  .home--container {
    width: 70%;
    margin: auto;
    border-left: solid 4px orange;
    border-right: solid 4px orange;
  }
}

@media (max-width: 1279px) {
  .recipeList--title {
    padding-top: 5rem;
  }

  .home {
    width: 100%;
  }
}
