.recipeButton {
  display: flex;
  flex-direction: row;
  padding: 30px;
  gap: 40px;
}

.recipeButton img {
  position: relative;
  transform: scale(0.8);
  border: solid black 2px;
  border-radius: 4px;
  top: -1.5rem;
  left: -0.9rem;
}

.recipeButton img:hover {
  transform: scale(0.9);
  border-bottom: solid 5px orange;
  border-right: solid 5px orange;
}

.recipeButton--title {
  text-align: center;
  margin: 2rem auto;
  width: 100%;
}

@media (max-width: 1279px) {
  .recipeButton {
    margin-top: 30px;
    padding: 0;
  }

  .recipeButton--title {
    margin: 0 0 1rem 0;
  }
}
